:root {
    --sr-white: #fff;
    --sr-light-gray: #f4f5f9;
    --sr-black: #000;
    --sr-primary: #5467ff;
    --sr-secondary: #01d7cf;
    --sr-danger: #f25601;
    --sr-yellow: #f4c703;
    --sr-green: #02db47;
    --sr-shadow-color: rgba(0, 0, 0, 0.1);
    --sr-text-color: #252525;
}
.sr-app * {
    box-sizing: border-box;
    margin: 0;
    outline: none;
    padding: 0;
}
.sr-app {
    background-color: #f4f5f9;
    background-color: var(--sr-light-gray);
    color: #252525;
    color: var(--sr-text-color);
    font-family: open sans, sans-serif;
    padding: 20px;
}

.adsbygoogle {
    display: none !important;
}

audio {
    width: 100%;
}

*::-webkit-scrollbar {
    border-radius: 0;
    height: 8px;
    width: 8px;
    transition: all 0.4s;
    opacity: 0;
}
*:hover::-webkit-scrollbar {
    border-radius: 0;
    height: 8px;
    width: 8px;
    opacity: 1;
    /* background-color: #d9d9d9; */
}

*::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
    background-color: rgba(0, 0, 0, 0);
}

*::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(22, 24, 35, 0.06);
    background-color: #d9d9d9;
    /* background-color: transparent; */
}
*hover::-webkit-scrollbar-thumb {
    background-color: #d9d9d9;
}

