    border-radius: 5px;
    color: #ffff;
    color: var(--sr-white);
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    transition: all 0.2s;
    -webkit-user-select: none; /* Safari */
