}
.sr-btn:hover {
    opacity: 0.85;
    transform: scale(0.9);
}
.sr-btn + .sr-btn {
    margin-left: 10px;
}
.card-container {
    background: #f4f5f9;
    background: var(--sr-light-gray);
    border-radius: 12px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    left: 50%;
    max-height: 95%;
    overflow: auto;
    padding: 20px;
    position: relative;
    position: fixed;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 90%;
    z-index: 9999;
}
.card-container::-webkit-scrollbar {
    display: none;
}
.card-container {
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.card-container .sr-card {
    width: calc(100% / 3 - 25px);
}

@media (max-width: 1200px) {
    .card-container {
        width: 97%;
    }
}

@media (max-width: 1000px) {
    .card-container .sr-card,
    .sr-search-container .sr-card {
        width: calc(100% / 2 - 25px) !important;
    }
}
@media (max-width: 500px) {
    .card-container .sr-card,
    .sr-search-container .sr-card {
        width: 100% !important;
    }

    .card-container {
        width: 100vw;
        height: 100vh;
        max-height: unset;
