.sr-card,
.form-container .sr-search-item {
    --space: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin: 10px;
    padding: var(--space);
    position: relative;
}
.sr-card:after,
.form-container .sr-search-item:after {
    color: #ecececb0;
    content: attr(data-level);
    font-size: 40px;
    font-weight: 700;
    line-height: 40px;
    position: absolute;
    right: calc(var(--space) + 8px);
    top: var(--space);
}

