.sr-progress-chart {
    align-items: flex-end;
    background-color: #f4f4f4ea;
    bottom: 50px;
    box-shadow: 5px 6px 20px rgba(0, 0, 0, 0.3);
    display: flex;
    height: 350px;
    justify-content: space-between;
    left: 50%;
    max-width: 650px;
    padding: 25px;
    position: fixed;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    width: 100%;
    z-index: 999;
}
.sr-progress-chart .bar {
    transition: height 1s ease-in;
}
.bar {
    -webkit-animation: progress-chart 2s ease-in-out;
    animation: progress-chart 2s ease-in-out;
    background-color: #07c;
    border: 1px solid #fff;
    border-radius: 4px;
    position: relative;
    width: 12%;
}
.bar + .bar {
    margin-left: 15px;
}
.bar:after {
    color: #01d7cf;
    color: var(--sr-secondary);
    content: attr(data-counter);
    top: -25px;
}
.bar:after,
.bar:before {
    left: 50%;
    position: absolute;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}
.bar:before {
    bottom: -25px;
    color: gray;
    content: attr(data-level);
}
.bar-empty {
    background-color: gray;
    border-radius: 999px 999px 5px 5px;
}
.progress-chart-close-btn.close-btn {
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 99999;
}
.close-btn {
    background-color: #e5e5e5;
    border: none;
    border-radius: 999px;
    color: #ccc;
    cursor: pointer;
    height: 20px;
    padding: 18px;
    position: relative;
    transition: all 0.3s ease-in-out;
    width: 20px;
}
.close-btn:hover {
    background-color: #dfdfdf;
    color: #01d7cf;
    color: var(--sr-secondary);
}
.close-btn:hover .close-icon {
    font-size: 24px;
}
.close-icon {
    font-size: 28px;
    left: 50%;
    line-height: 20px;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
.card-actions-container {
    bottom: 10px;
    display: flex;
    justify-content: center;
    left: 50%;
    position: fixed;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 99999;
}
.form-word.base-actions-container {
    align-items: center;
    display: flex;
    justify-content: center;
}
.form-word.base-actions-container .action-btn {
    border-radius: 999px;
    display: flex;
    padding: 10px;
    transition: all 0.5s;
}
.form-word.base-actions-container .action-btn + .action-btn {
    margin-left: 10px;
}
.form-word.base-actions-container .action-btn.btn-award {
    --fill-color: #ffe367;
}
.form-word.base-actions-container .action-btn.btn-flash-cards {
    --fill-color: var(--sr-secondary);
}
.form-word.base-actions-container .action-btn svg {
    fill: #cbcbcb;
    transition: all 0.3s;
    width: 20px;
}
.form-word.base-actions-container .action-btn:hover svg {
    fill: var(--fill-color);
}
@-webkit-keyframes progress-chart {
    0% {
        height: 0;
    }
    to {
        height: attr(data-height);
    }
}
@keyframes progress-chart {
    0% {
        height: 0;
    }
    to {
        height: attr(data-height);
    }
}

/* Nav mobile */
