/* Typing app */
.sr-matched {
    color: orange;
}
.sr-wrong {
    color: #8a8a8a7a;
}

.sr-caret-active {
    position: relative;
}

.sr-caret-active::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translate(0, 100%);
    display: inline-block;
    width: 100%;
    height: 4px;
    background-color: orange;
    animation: caret 1.2s infinite linear;
}

@keyframes caret {
    0%,
    100% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
}

.remaining {
    color: red;
}

input.sr-input-typing {
    margin-top: 20px;
    width: 300px;
    padding: 10px;
    width: 100%;
    padding: 6px 12px;
    background: rgb(31, 32, 35);
    border: 1px solid rgb(60, 63, 68);
    border-radius: 4px;
    font-size: 20px;
    color: rgb(247, 248, 248);
    height: 46px;
    appearance: none;
    transition: border 0.15s ease 0s;
}

.sr-typing-app {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999999999999999;
    width: 95vw;
    height: 95vh;

    background-color: var(--sr-black);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    font-weight: 700;
    font-size: 21pt;
    color: rgb(119, 136, 153);
    outline-color: rgb(119, 136, 153);
}

