    text-align: center;
    /* font-weight: normal; */
    color: orange;
}
.sr-typing-word-info-1 {
    /* position: fixed; */
    top: 0;
    left: 0;
    /* transform: translate(-50%, -117%); */
    font-size: 16px;
    padding: 7px 10px;
    display: block;
    margin: auto;
    min-width: max-content;
    width: max-content;
    background-color: white;
    box-shadow: 1px 2px #ccc;
    border-radius: 5px;
}
.sr-typing-word-info {
    position: fixed;
    top: 0;
    left: 0;
    transform: translate(-50%, -124%);
    font-size: 16px;

    padding: 7px 10px;
    display: block;
    min-width: max-content;
    background-color: white;
    box-shadow: 1px 2px #ccc;
    border-radius: 5px;
}

/* Search field */

.sr-search-container {
    width: 100%;
    order: -9999999999999;
}

.sr-search-results {
    display: flex;
    width: 100%;
    overflow-x: auto;
}

.sr-search-container .sr-card {
    width: calc(100% / 3 - 25px);
    flex-shrink: 0;
}

.sr-search-input {
    background: white;
    border: none;
    color: #808080a3;
    margin: 10px auto;
    padding: 10px;
    font-size: 20px;
    display: block;
    width: 50%;
    min-width: 150px;
    box-shadow: 1px 1px 10px #ccc;
    border-radius: 5px;
}

span#spelling {
    text-wrap: nowrap;
}

/* .highlight {
    background-color: #f06;
    color: white;
} */
.highlight {
    text-decoration: underline;
    text-decoration-color: rgb(255, 0, 102);
}

.sr-card-similar-words:hover,
.over-time:hover,
.sr-html-symbol-btn:hover {
    text-decoration: underline;
    transition: all 0.2s;
}

.sr-tag-words-container .sr-tag-word {
    position: relative;
    padding: 2px 5px;
    border-radius: 2px;
    background: #0b8bbb;
}

.sr-tag-words-container .sr-tag-word:hover {
    transform: unset;
}

.sr-tag-word + .sr-tag-word {
    margin-left: 3px;
}

.sr-tag-word:hover {
    opacity: 1;
}

.sr-tag-word:hover::after {
    content: attr(data-info);
    position: absolute;
    top: -2px;
    left: 50%;
    transform: translate(-50%, -100%);
    background-color: var(--sr-secondary);
    background-color: #ea745c;
    padding: 5px;
    display: block;
    max-width: 115px;
    width: max-content;
    border-radius: 4px;
}

.sr-tag-word.sr-similar-meaning {
    background-color: #e78954 !important;
    background-color: #03c8cb !important;
    background: unset !important;
    border: 1px solid currentColor;
    color: #005b88;
    border-radius: 3px;
}

.sr-my-sentence {
    /* background: var(--sr-secondary); */
    padding: 3px 4px;
    border-radius: 3px;
    /* color: white; */
    line-height: 16px;
    margin: 0px 3px 3px 0px;
    text-decoration: underline 2px solid var(--sr-secondary);
}

.sr-my-sentence .sr-symbol {
    color: orange;
    margin-right: 6px;
    font-weight: bold;
    font-size: 18px;
}

.sr-create-sentence-input {
    width: 80%;
    margin-left: 0;
    margin: auto;
    text-align: center;
    color: #616161;
    border-color: var(--sr-shadow-color);
}

.sr-create-sentence-input::first-letter,
.sr-my-sentence::first-letter {
    text-transform: uppercase;
}

.sr-html-symbol-btn {
    border-radius: 999px;
    padding: 4px 11px !important;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #cbcbcb;
}

.sr-html-symbol-btn:hover {
    color: var(--sr-secondary);
    transform: scale(0.9);
}

.sr-html-symbol {
    --size: 20px;
    font-size: var(--size);
    line-height: var(--size);
}

.sr-copy-btn {
    border-radius: 999px;
    padding: 2px;
    color: #3f51b5;

    /* border: 1px solid; */
    /* padding: 3px !important; */
    /* width: 23px; */
    /* height: 23px;*/
}

.sr-flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.sr-tag-words-container .sr-word-synonym {
    /* background: #ff7f00; */
    background: none;
    color: #ff7f00;
    border: 1px solid #ff7f00;
}

.sr-circle-btn {
    --size: 50px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: var(--size);
    height: var(--size);
    color: #cbcbcb;
    border-radius: 9999px;
    font-size: 24px;
    padding: 0;
    /* background: #8484841f; */
}

.sr-loader {
    --size: 48px;
    width: var(--size);
    height: var(--size);
    display: inline-block;
    position: relative;
}
.sr-loader::after,
.sr-loader::before {
    content: "";
    width: var(--size);
    height: var(--size);
    border: 2px solid var(--sr-secondary);
    position: absolute;
    left: 0;
    top: 0;
    box-sizing: border-box;
    animation: rotation 2s ease-in-out infinite;
}
.sr-loader::after {
    border-color: var(--sr-primary);
    animation-delay: 1s;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.sr-get-words-input {
    border: none;
    /* border-left: 1px solid; */
    outline: none;
    padding: 5px;
    font-size: 20px;
    color: #808080;
    text-align: center;
    width: 50px;
}

/* Dark mode */
.sr-dark-mode {
    --sr-white: #424242;
    --sr-light-gray: #0d1017;
    --sr-light-gray: #212121;
    --sr-black: #fff;
    --sr-text-color: #f4f5f9;
    --sr-primary: #2f81f7;
    --sr-secondary: #03dac5;
    --sr-danger: #f25601;
    --sr-yellow: #bb86fc;
    --sr-green: #39d353;
    /* --sr-shadow-color: rgba(214, 214, 214, 0.1); */
}

.sr-dark-mode .sr-title {
    color: var(--sr-text-color);
    color: #e6edf3;
}

.sr-dark-mode .meaning,
.sr-dark-mode .sr-meaning-flag,
.sr-dark-mode #examples,
.sr-dark-mode #note,
.sr-dark-mode .sr-title,
.sr-dark-mode .sr-create-sentence-input,
.sr-card-item-image-description {
    color: #7d8590;
    color: #afafaf;
}

.sr-dark-mode .sr-overlay {
    background: var(--sr-light-gray);
}

.card-container::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
}

/* Beside */
.sr-beside-wrapper .sr-overlay {
    display: none;
}

.sr-beside-wrapper .card-container {
    max-width: 25%;
    min-width: 380px !important;
    transform: translateY(-50%);
}

.sr-beside-wrapper.sr-side-left .card-container {
    left: 10px;
}
.sr-beside-wrapper.sr-side-right .card-container {
    right: 10px;
    left: unset;
}

.sr-beside-wrapper .card-container .sr-card,
.sr-beside-wrapper .sr-search-container .sr-card {
    width: 100% !important;
}

/* Print window */
h1.sr-print-window-title {
    font-size: 20px;
    text-align: center;
    margin-bottom: 15px;
    font-family: cursive;
}
.sr-print-window-container {
    max-width: 1200px;
    margin: auto;
    background-color: white;
}

.sr-print-window-words-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.sr-print-window-words-container > * {
    --space: 5px;
    max-width: calc(100% / 3 - 25px);
    margin: 0 10px;
    flex-grow: 1;
}

@media (max-width: 500px) {
    .sr-print-window-words-container > * {
        max-width: unset;
        width: calc(100% / 2 - 25px);
    }
}

.sr-print-window {
    font-size: 12px;
}

.sr-print-window-words-container h2#voca {
    font-size: 15px;
}

.sr-print-window-words-container span.type-words {
    color: #3b3b3b;
}

.sr-print-window-words-container .sr-btn.sr-tag-word {
    font-size: 14px;
}

.sr-print-window-words-container .sr-tag-word {
    margin-top: 2px;
    margin-bottom: 2px;
}

.sr-print-window-words-container span#meaning {
    color: #626262;
}

.sr-print-window-words-container div#examples,
div#note {
    color: #242410;
}

.sr-print-window-words-container .sr-passage {
    flex: 1;
    min-width: unset;
    border: 1px solid var(--sr-secondary);
    padding: 10px;
    width: 98%;
}

.sr-print-window-words-container .sr-passage * {
    background: transparent !important;
    color: black !important;
}

.sr-print-window-words-container .sr-passage .highlight {
    color: var(--sr-secondary) !important;
    text-decoration: none;
}

@media print {
    .sr-print-word-delete,
    .sr-no-print {
        display: none !important;
    }

    body.sr-print-missed-words {
        background-color: transparent !important;
    }

    body.sr-print-missed-words > * {
        display: none !important;
    }

    body.sr-print-missed-words .sr-print-component {
        display: block !important;
    }
}

.sr-words-definition {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
}

.sr-border-bottom {
    border-bottom: 1px solid var(--sr-secondary);
    height: 25px;
}

.sr-20-percent {
    width: 20%;
    max-width: 20%;
}

.sr-words-definition > div {
    flex: 1;
    margin: 0 7px;
}

.sr-words-definition h4 {
    color: var(--sr-secondary);
}

.sr-words-definition-title-container {
    display: flex;
    /* justify-content: space-between; */
    align-self: start;
}

h4.sr-border-bottom {
    flex: 1;
    /* margin-left: 5px; */
}

.sr-words-definition-container {
    display: flex;
    flex: 1;
}

.sr-definition {
    flex: 1;
    margin-left: 7px;
}

.sr-underline {
    text-decoration: underline;
}

.sr-card-item-trans-word {
    font-size: 18px;
    font-weight: bold;
    line-height: 21px;
}

.sr-card-item-image {
    max-width: 100%;
    border-radius: 5px;
    margin: 5px 0;
}
.sr-card-item-image-description {
    text-align: end;
    font-size: 14px;
}
.sr-card-item-image-description a {
    text-decoration: underline;
    color: var(--sr-secondary);
}

.sr-card-items-footer {
    flex-direction: column;
    align-items: end;
    justify-content: end;
    flex: 1;
}

#sr-album {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 3px;
}

#sr-album ul.stage {
    padding-left: 0;
    white-space: nowrap;
    overflow: auto;
    max-width: 80%;
