    z-index: 99999999;
}

/* Simple card */

.sr-card-simple #sr-album,
.sr-card-simple #meaning,
.sr-card-simple .sr-meaning-flag,
.sr-card-simple .sr-additional-actions,
.sr-card-simple .sr-card-items-footer,
.sr-card-simple .sr-copy-btn {
